@import url('https://fonts.googleapis.com/css2?family=Rubik:ital,wght@0,300;0,500;0,900;1,900&display=swap');

:root {
  --ff-light: 'Rubik Light';
  --ff-medium: 'Rubik Medium';
  --ff-bold: 'Rubik Black';
  --cr-text: #fff;
  --shadow-hover: 1px 1px 2px #000, 0 0 25px blue, 0 0 5px darkblue;
  --height-hero: 1024px;
}

* {
  margin: 0;
  padding: 0;
  font: inherit;
  /* outline: solid 1px red; */
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  width: 100%;
  height: 100%;
}

body {
  margin: 0;
  font-family: var(--ff-light), -apple-system, BlinkMacSystemFont, 'Segoe UI',
    'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans',
    'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

p {
  font-family: var(--font-light);
  font-style: normal;
  font-weight: 300;
  font-size: 14px;
}

ul,
ol {
  list-style: none;
}

a {
  color: inherit;
  text-decoration: none;
}

button {
  border: none;
  outline: none;
}
